.container {
	.col {
		&--two {
			@include make-column(20);
			@include display-for(xs) {
				@include make-column(40); } }
		&--three {
			@include make-column(13.3);
			@include display-for(sm) {
				@include make-column(20); }
			@include display-for(xs) {
				@include make-column(40); } }
		&--four {
			@include make-column(10);
			@include display-for(sm) {
				@include make-column(20); }
			@include display-for(xs) {
				@include make-column(40); } } }
	.col--two, .col--three, .col--four {
		padding: 20px; } }
